/* ======= HEROS ======= */

.cl-hero * {
    box-sizing: border-box;
}

.cl-hero {
    & {
        width: 100%;
        height: 300px;
        overflow: hidden;
        position: relative;
        background: #002147;
        transition: height .2s ease-in-out;
        z-index: 1;
        box-sizing: border-box;
    }
    
    &.fullwidth,
    &.super {
        width: 100vw;
        position: relative;
        left: 50%;
        margin-left: -50vw;
    }

    &.super {
        height: 100vh;
    }
    
    iframe {
        z-index: 1;
        position: relative;
        max-width: initial;
        max-height: initial;
    }

    .still,
    .animate,
    .poster {
        z-index: 2;
        width: 100%;
        height: 100%;
        position: absolute;
        background-repeat: no-repeat;
        background-color: transparent;
        background-size: cover;
        background-position: center center;
    }
    
    .animate {
        transition: all 10s ease-in-out;
    }
    
    .poster {
        opacity: 1;
        transition: all .4s ease-in-out;
    }
    
    .poster.unveil {
        opacity: 0;
        transition-delay: .5s;
    }
    
    .overlay {
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: 3;
    }
    
    .block {
        position: absolute;
        z-index: 3;
        bottom: 0;
        left: 0;
        right: 0;
        text-shadow: 0 0 10px rgba(0,0,0,.9);
        padding: 2rem 5% 5%;
        background: linear-gradient(to bottom, rgba(0,0,0,0), rgba(0,0,0,.4) 3rem);
    }

    h1 {
        position: relative;
        text-align: left;
        font-weight: 700;
        font-size: 1.8rem;
        color: $white;
        transform: all .2s ease-in-out;
        padding: 0;
        margin: 0;
    }

    p {
        color: #fff;
        font-weight: 400;
        font-size: 1.1rem;
    }
    
    a.cl-button,
    span.cl-button {
        & {
            margin-top: 2rem;
            border: none;
            background: rgba(255,255,255,.9);
            box-shadow: $level3;
            position: relative;
            top: 0;
        }

        &:hover,
        &:focus {
            background: $white;
            border: none;
            color: $link;
            top: -3px;
        }
    }
    
    .motionswitch {
        & {
            display: none;
            position: absolute;
            right: 5%;
            top: 5%;
            z-index: 5;
            opacity: 0;
            width: 40px;
            height: 40px;
            background: #000;
            border: 1px solid #fff;
            border-radius: 50%;
            transition: all .3s ease-in-out;
        }
        &:before,
        &:after {
            content: '';
            position: absolute;
            top: 9px;
            left: 11px;
            height: 20px;
            border-style: solid;
            border-width: 0 0 0 6px;
            border-color: transparent transparent transparent #fff;
            transition: all .2s ease-in-out;
        }
        &:after {
            left: 21px;
        }
    }

    &.paused .motionswitch {
        &:before {
            height: 10px;
            border-width: 5px 0 5px 10px;
            border-color: transparent transparent transparent #fff;
        }
        &:after {
            top: 14px;
            height: 0;
            border-width: 5px 0 5px 10px;
            border-color: transparent transparent transparent #fff;
        }
    }

    .overlay:hover .motionswitch,
    .overlay:focus .motionswitch {
        opacity: .4;
    }

    .overlay .motionswitch:hover,
    .overlay .motionswitch:focus {
        cursor: pointer;
        opacity: .8;
    }

}

@supports ((-webkit-backdrop-filter: initial) or (backdrop-filter: initial)) {
    .cl-hero {
        a.cl-button,
        span.cl-button { 
            backdrop-filter: saturate(180%) blur(10px); 
            background-color: rgba(255,255,255,.7);
        }
    }
}

@media screen and (min-width: 750px) {
    .cl-hero .motionswitch {
        display: block;
    }  
}

@media screen and (min-width: $break1) {
    .cl-hero {
        & {
            height: 400px;
        }
        h1 {
            font-size: 2.2rem;
        }
        p {
            font-size: 1.3rem;
        }
    }
}

@media screen and (min-width: $break2) {
    .cl-hero {
        & {
            height: 500px;
        }
        h1 {
            font-size: 2.8rem;
        }
        p {
            font-size: 1.5rem;
        }
    }
}

